.about {
    background: url('./collage2.jpg');
    background-repeat: repeat;
    background-position: left;
    height: 100vh;
    width: 100vw;

    position: fixed;
    top: -100vh;
    left: 0px;

    z-index: 2;

    transition: top 0.8s;
}

.about-slide{
    top: 0px;
}

.close-about {
    cursor: pointer;
    position: absolute;
    top: 10px;
    right: 10px;
    width: 50px;
    height: 50px;
    border-radius: 50px;
    background: black;
    color: white;
    font-size: 16px;
    font-weight: bold;
    border: 3px solid black;
}

.close-about:hover {
    border: 3px solid white;
}

.about-container {
    background: #151515;
    color: white;
    width: 600px;
    height: 75vh;
    padding: 180px 50px 0px;
    border-radius:  20px 20px 0px 0px;
    box-sizing: border-box;

    position: absolute;
    bottom: -200vh;
    left: calc(50% - 300px);

    transition: bottom 0.8s;
}

.about-container-slide {
    bottom: 0px;
}

.about-content {
    height: 100%;
    box-sizing: border-box;
    overflow: scroll;
    border-top: 1px solid white;
    padding-bottom: 20px;
}

.about-content h4 {
    font-family: "PT Mono";
    text-align: center;
    font-size: 20px;
    line-height: 30px;
    font-style: italic;
    font-weight: normal;
    color: rgb(59, 59, 59);
}

.about-content p {
    padding-right: 50px;
    font-size: 15px;
    line-height: 30px;
}

.about-content img {
    width: 100%;
}

@media only screen and (max-width: 730px) {
    .about-container{
        width: 100%;
        border-radius: 0px;
        left: 0px;
        padding: 180px 20px 0px;
    }

    .about-content {
        padding-bottom: 50px;
    }
}

@media only screen and (max-width: 450px) {
    .close-about {
        top: -50px
    }

    .about-container{
        height: 90%;
        padding: 160px 20px 0px;
    }

    .about-content h4 {
        font-size: 13px;
        line-height: 20px;
    }

    .about-content {
        padding-bottom: 50px;
    }
}