@import url(https://fonts.googleapis.com/css?family=PT+Mono);
@import url(https://fonts.googleapis.com/css?family=Aldrich);
@import url(https://fonts.googleapis.com/css?family=Roboto);
/* Document
 * ========================================================================== */
/**
 * 1. Correct the line height in all browsers.
 * 2. Prevent adjustments of font size after orientation changes in
 *    IE on Windows Phone and in iOS.
 */
html {
  line-height: 1.15; /* 1 */
  -ms-text-size-adjust: 100%; /* 2 */
  -webkit-text-size-adjust: 100%; /* 2 */
}
/* Sections
 * ========================================================================== */
/**
 * Correct the font size and margin on `h1` elements within `section` and
 * `article` contexts in Chrome, Edge, Firefox, and Safari.
 */
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
/* Grouping content
 * ========================================================================== */
/**
 * Remove the margin on nested lists in Chrome, Edge, IE, and Safari.
 */
dl dl,
dl ol,
dl ul,
ol dl,
ul dl {
  margin: 0;
}
/**
 * Remove the margin on nested lists in Edge 18- and IE.
 */
ol ol,
ol ul,
ul ol,
ul ul {
  margin: 0;
}
/**
 * 1. Add the correct box sizing in Firefox.
 * 2. Show the overflow in Edge 18- and IE.
 */
hr {
  box-sizing: content-box; /* 1 */
  height: 0; /* 1 */
  overflow: visible; /* 2 */
}
/**
 * Add the correct display in IE.
 */
main {
  display: block;
}
/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */
pre {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}
/* Text-level semantics
 * ========================================================================== */
/**
 * Add the correct text decoration in Edge 18-, IE, and Safari.
 */
abbr[title] {
  text-decoration: underline;
  text-decoration: underline dotted;
}
/**
 * Add the correct font weight in Chrome, Edge, and Safari.
 */
b,
strong {
  font-weight: bolder;
}
/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */
code,
kbd,
samp {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}
/**
 * Add the correct font size in all browsers.
 */
small {
  font-size: 80%;
}
/* Embedded content
 * ========================================================================== */
/**
 * Hide the overflow in IE.
 */
svg:not(:root) {
  overflow: hidden;
}
/* Forms
 * ========================================================================== */
/**
 * Remove the margin on controls in Safari.
 */
button,
input,
select {
  margin: 0;
}
/**
 * 1. Show the overflow in IE.
 * 2. Remove the inheritance of text transform in Edge 18-, Firefox, and IE.
 */
button {
  overflow: visible; /* 1 */
  text-transform: none; /* 2 */
}
/**
 * Correct the inability to style buttons in iOS and Safari.
 */
button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}
/**
 * Correct the padding in Firefox.
 */
fieldset {
  padding: 0.35em 0.75em 0.625em;
}
/**
 * Show the overflow in Edge 18- and IE.
 */
input {
  overflow: visible;
}
/**
 * 1. Correct the text wrapping in Edge 18- and IE.
 * 2. Correct the color inheritance from `fieldset` elements in IE.
 */
legend {
  box-sizing: border-box; /* 1 */
  color: inherit; /* 2 */
  display: table; /* 1 */
  max-width: 100%; /* 1 */
  white-space: normal; /* 1 */
}
/**
 * 1. Add the correct display in Edge 18- and IE.
 * 2. Add the correct vertical alignment in Chrome, Edge, and Firefox.
 */
progress {
  display: inline-block; /* 1 */
  vertical-align: baseline; /* 2 */
}
/**
 * Remove the inheritance of text transform in Firefox.
 */
select {
  text-transform: none;
}
/**
 * 1. Remove the margin in Firefox and Safari.
 * 2. Remove the default vertical scrollbar in IE.
 */
textarea {
  margin: 0; /* 1 */
  overflow: auto; /* 2 */
}
/**
 * 1. Correct the odd appearance in Chrome, Edge, and Safari.
 * 2. Correct the outline style in Safari.
 */
[type="search"] {
  -webkit-appearance: textfield; /* 1 */
  outline-offset: -2px; /* 2 */
}
/**
 * Correct the cursor style of increment and decrement buttons in Safari.
 */
::-webkit-inner-spin-button,
::-webkit-outer-spin-button {
  height: auto;
}
/**
 * Correct the text style of placeholders in Chrome, Edge, and Safari.
 */
::-webkit-input-placeholder {
  color: inherit;
  opacity: 0.54;
}
/**
 * Remove the inner padding in Chrome, Edge, and Safari on macOS.
 */
::-webkit-search-decoration {
  -webkit-appearance: none;
}
/**
 * 1. Correct the inability to style upload buttons in iOS and Safari.
 * 2. Change font properties to `inherit` in Safari.
 */
::-webkit-file-upload-button {
  -webkit-appearance: button; /* 1 */
  font: inherit; /* 2 */
}
/**
 * Remove the inner border and padding of focus outlines in Firefox.
 */
::-moz-focus-inner {
  border-style: none;
  padding: 0;
}
/**
 * Restore the focus outline styles unset by the previous rule in Firefox.
 */
:-moz-focusring {
  outline: 1px dotted ButtonText;
}
/**
 * Remove the additional :invalid styles in Firefox.
 */
:-moz-ui-invalid {
  box-shadow: none;
}
/* Interactive
 * ========================================================================== */
/*
 * Add the correct display in Edge 18- and IE.
 */
details {
  display: block;
}
/*
 * Add the correct styles in Edge 18-, IE, and Safari.
 */
dialog {
  background-color: white;
  border: solid;
  color: black;
  display: block;
  height: -moz-fit-content;
  height: -webkit-fit-content;
  height: fit-content;
  left: 0;
  margin: auto;
  padding: 1em;
  position: absolute;
  right: 0;
  width: -moz-fit-content;
  width: -webkit-fit-content;
  width: fit-content;
}
dialog:not([open]) {
  display: none;
}
/*
 * Add the correct display in all browsers.
 */
summary {
  display: list-item;
}
/* Scripting
 * ========================================================================== */
/**
 * Add the correct display in IE.
 */
template {
  display: none;
}
/* User interaction
 * ========================================================================== */

html, body, #root, .app {
    box-sizing: border-box;
    padding: 0px;
    margin: 0px;

    font-size: 13px;
    line-height: 20px;
    font-family: sans-serif;
    background: black;
    height: 100%;
}

h1 {
    font-family: 'PT Mono';
}


@media only screen and (min-width: 1700px) {
    html, body, .app, .single-content, .single-content li, .about-content p, .skills-content, nav li {
        font-size: 18px !important;
        line-height: 35px;
    }

    .project-links svg {
        margin-right: 10px !important;
    }
}
.about {
    background: url(/static/media/collage2.e4e18595.jpg);
    background-repeat: repeat;
    background-position: left;
    height: 100vh;
    width: 100vw;

    position: fixed;
    top: -100vh;
    left: 0px;

    z-index: 2;

    transition: top 0.8s;
}

.about-slide{
    top: 0px;
}

.close-about {
    cursor: pointer;
    position: absolute;
    top: 10px;
    right: 10px;
    width: 50px;
    height: 50px;
    border-radius: 50px;
    background: black;
    color: white;
    font-size: 16px;
    font-weight: bold;
    border: 3px solid black;
}

.close-about:hover {
    border: 3px solid white;
}

.about-container {
    background: #151515;
    color: white;
    width: 600px;
    height: 75vh;
    padding: 180px 50px 0px;
    border-radius:  20px 20px 0px 0px;
    box-sizing: border-box;

    position: absolute;
    bottom: -200vh;
    left: calc(50% - 300px);

    transition: bottom 0.8s;
}

.about-container-slide {
    bottom: 0px;
}

.about-content {
    height: 100%;
    box-sizing: border-box;
    overflow: scroll;
    border-top: 1px solid white;
    padding-bottom: 20px;
}

.about-content h4 {
    font-family: "PT Mono";
    text-align: center;
    font-size: 20px;
    line-height: 30px;
    font-style: italic;
    font-weight: normal;
    color: rgb(59, 59, 59);
}

.about-content p {
    padding-right: 50px;
    font-size: 15px;
    line-height: 30px;
}

.about-content img {
    width: 100%;
}

@media only screen and (max-width: 730px) {
    .about-container{
        width: 100%;
        border-radius: 0px;
        left: 0px;
        padding: 180px 20px 0px;
    }

    .about-content {
        padding-bottom: 50px;
    }
}

@media only screen and (max-width: 450px) {
    .close-about {
        top: -50px
    }

    .about-container{
        height: 90%;
        padding: 160px 20px 0px;
    }

    .about-content h4 {
        font-size: 13px;
        line-height: 20px;
    }

    .about-content {
        padding-bottom: 50px;
    }
}

.home {
    background: #151515;
    width: 100vw;
    height: 100%;
    display: flex;
    flex-direction: row;
}

.sidebar, .content {
    width: 50vw;
    box-sizing: border-box;
}

@media only screen and (min-width: 2000px) {
    .sidebar{
        width: calc( 100% - 1000px) !important;
    }

    .content {
        width: 1000px !important;
    }

    .about-container {
        width: 1000px !important;
        left: calc(50% - 500px) !important;
    }
}

@media only screen and (max-width: 950px) {
    .sidebar {
        width: calc(100% - 600px);
    }
    .content {
        width: 600px;
    }
}


@media only screen and (max-width: 730px) {
    .home {
        flex-direction: column;
    }
}
.sidebar {
    /* background: url('./path.jpg'); */
    background: url('https://scontent-lga3-2.cdninstagram.com/v/t51.2885-15/e35/39323464_296104134311533_3166859299225862144_n.jpg?_nc_ht=scontent-lga3-2.cdninstagram.com&_nc_cat=101&_nc_ohc=xF8n_8ZuLMoAX9un2ux&tp=1&oh=822bc131223790479e87a432b694a80b&oe=603793E8');
    background-repeat: no-repeat;
    background-size: cover !important;
    background-position: left !important;

    position: relative;
}

.email {
    display: inline-block;
    color: rgb(128, 128, 128);
    text-decoration: none;
    padding: 2px 15px 0px 0px;
}

.email:hover {
    color: white;
}

.logos {
    box-sizing: border-box;
    padding: 10px;
    position: fixed;
    top: 0px;
    right: 0px;

    display: flex;
}

.logos a {
    text-decoration: none;
    margin-right: 10px;
}

.logos a svg {
    width: 20px;
    fill: rgb(128, 128, 128);
}

.logos a svg:hover {
    fill: white;
}

nav {
    transform: rotate(-90deg);
    font-size: 15px;
    position: absolute;
    background: black;
    padding: 10px 20px;
    box-sizing: border-box;
    border-radius: 5px 5px 0px 0px;


    bottom: 250px;
    right: -110px;

    /* when adding all projects link
    right: -160px; */

    width: 280px;
}

nav li {
    cursor: pointer;
    display: inline-block;
    text-decoration: none;
    color: rgb(128, 128, 128);
    padding: 10px 15px;
    box-sizing: border-box;
    border-right: 1px solid rgb(42, 42, 42);
    font-size: 15px;
}

nav li a {
    color: rgb(128, 128, 128);
    margin: 0px;
    padding: 0px;
    text-decoration: none;
}

nav li:first-child {
    border-radius: 10px 0px 0px 10px;
}

nav li:last-child {
    border-right: none;
    border-radius: 0px 10px 10px 0px;
}

nav li:hover, nav li a:hover {
    color: white;
}

@media only screen and (max-width: 730px) {

    .sidebar, .content {
        width: 100%;
    }

    .email {
        width: 100%;
    }

    .logos, nav {
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
    }

    .logos {
        position: static;
        position: initial;
        width: 100%;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        padding: 50px 0px;
    }

    .logos, .sidebar nav {
        width: 100%;
    }

    .email {
        margin-bottom: 15px;
        text-align: center;
        margin-right: 0px !important;
        padding-right: 0px;
    }

    .sidebar {
        display: flex;
        flex-direction: column-reverse;
        justify-content: center;
        position: fixed;
        z-index: 10;
        right: 0px;
        top: -100%;
        background: #151515 !important;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        transition: top 0.5s;
    }

    .sidebar-down {
        top: 0px;
    }

    .contact-close, .contact-label {
        background: none;
        border: none;
        font-size: 13px;
        font-weight: normal;
        cursor: pointer;
    }

    .contact-close {
        position: absolute;
        bottom: 0px;
        right: 0px;
        padding: 10px 20px;
        background: black;
        color: white;
    }

    .contact-label {
        display: block;
        position: fixed;
        top: 0px;
        right: 0px;
        padding: 20px;
        fill: white;
    }

    .sidebar nav {
        position: relative;
        transform: none;
        bottom: auto;
        right: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 50px;
        background: none;
    }

    .sidebar nav li {
        color: rgb(128, 128, 128);
        border: none;
        padding: 10px;
        font-size: 19px;
    }

    .sidebar nav li:hover {
        color: white;
        background: none;
        border-radius: 0px;
    }
}
.content {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    height: 100%;
}

.info, .projects {
    box-sizing: border-box;
    width: 100%;
    height: 100%;
}
.info {
    font-family: "PT Mono";
    font-size: 16px;
    font-weight: normal;

    box-sizing: border-box;
    color: rgb(128, 128, 128);
    position: static;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: linear-gradient(#121212, black);
}

.info h1 {
    color: #E34344;
    font-size: 20px;
    font-weight: normal;
}

.info i {
    color: white;
}

.info p{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: auto;
    font-size: 14px;
    line-height: 25px;
    font-weight: normal;
}

.profile-pic {
    pointer-events: none; 
    box-sizing: border-box;
    border: 5px solid #151515;
    overflow: hidden;
    width: 100px;
    height: 100px;
    border-radius: 100px;

    top: calc(25% - 100px/2 );
    left: calc( 50% - 100px/2 );
    position: absolute;

    z-index: 5;

    transition: all 0.8s;
    pointer-events: none;
    touch-action: none;
}

.profile-pic img {
    width: 100%;
    pointer-events: none;
    touch-action: none;
}

.profile-pic-about {
    width: 300px;
    height: 300px;
    border-radius: 300px;

    top: calc(25% - 300px/2 );
    left: calc( 50% - 300px/2 );
    pointer-events: none;
    touch-action: none;
}

@media only screen and (min-height: 1200px) {
    .profile-pic {
        top: calc(25% - 150px/2 );
        left: calc( 50% - 150px/2 );

        width: 150px;
        height: 150px;
        border-radius: 150px;
    }

    .profile-pic-about {
        width: 300px;
        height: 300px;
        border-radius: 300px;

        top: calc(25% - 300px/2 );
        left: calc( 50% - 300px/2 );
    }
}

@media only screen and (min-width: 2000px) {
    .profile-pic {
        left: calc(100% - 1100px);
        top: calc(25% - 200px/2 );

        width: 200px;
        height: 200px;
        border-radius: 200px;
    }

    .profile-pic-about {
        left: calc( 50% - 200px );
        top: calc(25% - 225px );
        width: 400px;
        height: 400px;
        border-radius: 400px;
    }
}

@media only screen and (max-width: 950px) {
    .profile-pic {
        left: calc(100% - 650px);
    }

    .profile-pic-about {
        left: calc( 50% - 300px/2 );
    }
}

@media only screen and (max-width: 730px) {
    .profile-pic {
        top: 25px;
        left: calc( 50% - 100px/2 );
    }

    .profile-pic-about {
        top: calc(25% - 300px/2 );
        left: calc( 50% - 300px/2 );
    }
}

@media only screen and (max-width: 450px) {
    .profile-pic {
        width: 125px;
        height: 125px;
        top: 20px;
        left: calc( 50% - 125px/2 );
    }

    .profile-pic-about {
        top: 5px;
        left: calc( 50% - 200px/2 );
        width: 200px;
        height: 200px;
        border-radius: 200px;
    }

    .info {
        padding-top: 130px;
        text-align: center;
    }

    .info h1 {
        font-size: 20px;
        margin: 0px;
    }

    .info p {
        font-size: 20px;
        line-height: 25px;
    }
}
.projects {
    background: black;
    color: white;

    box-sizing: border-box;
    padding: 10px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.single {
    box-sizing: border-box;
    width: calc(50% - 5px);
    height: calc(50% - 5px);
    border-radius: 5px;
}


/* top row */
.single:nth-child(1), .single:nth-child(2){
    margin-bottom: 5px;
}

/* bottom row */
.single:nth-child(3), .single:nth-child(4){
    margin-top: 5px;
}

/* left column */
.single:nth-child(1), .single:nth-child(3){
    margin-right: 5px;
}

/* right column */
.single:nth-child(2), .single:nth-child(4){
    margin-left: 5px;
}

@media only screen and (min-height: 1200px) {
    .info{
        height: calc(100% - 700px) ;
    }
    .projects {
        height: 700px;
    }
}

.single {
    border-radius: 10px;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    overflow: hidden;
}

.single-content, .single-project {
    height: 100%;
    width: 50%;
    box-sizing: border-box;
}

.single-content {
    background: linear-gradient(rgba(0,0,0,0), rgba(0,0,0,0.5));
    font-size: 14px;
    box-sizing: border-box;
    padding: 20px 15px 10px;
    display: flex;
    flex-direction: column;
}

.single-content h3 {
    margin: 0px;
    color: white;
    font-weight: normal;
    margin-bottom: 5px;

    font-family: "PT Mono";
    font-size: 15px;
}

.single-content ul {
    margin: 0px;
    padding: 0px;
}

.single-content li {
    list-style: none;
    display: block;
    font-size: 13px;
}

.single-content li:first-child {
    padding-left: 0px;
}
.single-content li:last-child {
    border-right: 0px;
}



/* PROJECT */

.single-project {
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
}

.project-image {
    height: 100%;
    background-repeat: no-repeat;
    background-position: left;
    background-size: cover;
}

.description {
    background: linear-gradient(rgba(0,0,0,0), rgba(0,0,0,0.7));
    height: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    overflow: scroll;
    box-sizing: border-box;
}

.description p {
    margin: 0px;
    padding: 15px;
    padding-bottom: 0px;
    box-sizing: border-box;
}

.project-links {
    margin-top: 10px;
    box-sizing: border-box;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.project-links  svg{
    fill: white;
    width: 20px;
    margin-right: 5px;
    box-sizing: border-box;
}

.project-links  a {
    width: 100%;
    font-size: 15px;
    padding: 5px 10px;
    box-sizing: border-box;

    text-decoration: none;
    color: white;

    display: flex;
    align-items: center;
}

.project-links a:hover {
    background: rgba(0,0,0,0.2);
}

@media only screen and (max-width: 450px) {
    .projects {
        padding: 2px;
    }

    .single {
        flex-direction: column;
        width: calc( 50% - 2px);
        height: calc( 50% - 2px);
        border-radius: 10px;
    }

    /* top row */
    .single:nth-child(1), .single:nth-child(2){
        margin-bottom: 1px;
    }

    /* bottom row */
    .single:nth-child(3), .single:nth-child(4){
        margin-top: 1px;
    }

    /* left column */
    .single:nth-child(1), .single:nth-child(3){
        margin-right: 1px;
    }

    /* right column */
    .single:nth-child(2), .single:nth-child(4){
        margin-left: 1px;
    }

    .single-content, .single-project {
        width: 100%;
    }

    .single-project {
        height: 66%;
    }

    .single-content {
        height: 33%;
        padding: 0px;
        display: flex;
        justify-content: center;
    }

    .single-content h3 {
        margin: 0px;
        padding: 10px;
        display: flex;
    }

    .single-content ul, .description p {
        display: none
    }

    .description {
        overflow: auto;
    }

    .project-links {
        margin: 0px;
        height: 100%;
        display: flex;
        flex-direction: column;
    }

    .project-links a {
        height: 100%;
    }

    .project-links svg {
        margin-right: 10px;
    }
}
.skills {
    width: 100vw;
    height: 100vh;

    position: fixed;
    top: 0px;
    left: 0px;

    background: rgba(0,0,0,0.8);
    z-index: 10;

    transition: background 0.8s;
}

.skills-content {
    width: 400px;
    height: 100vh;
    padding: 50px 25px;
    box-sizing: border-box;
    background: white;

    position: absolute;
    top: 0px;
    left: -400px;

    font-size: 15px;
    line-height: 25px;
    text-align: right;

    transition: left 0.8s;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.skills-slide {
    left: 0px;
}

ul, li {
    margin: 0px;
    padding: 0px;
    list-style-type: none ;
}

label {
    font-family: PT Mono;
    display: block;
    font-weight: bold;
    margin-top: 25px;
}

.hide {
    visibility: hidden;
}

.fade-out {
    background: none;
}

@media only screen and (max-width: 450px) {
    .skills-content {
        width: 80%;
        align-items: flex-start;
        padding: 10px 20px;
    }

    .skills-content label {
        margin-top: 10px;
    }
}
