.skills {
    width: 100vw;
    height: 100vh;

    position: fixed;
    top: 0px;
    left: 0px;

    background: rgba(0,0,0,0.8);
    z-index: 10;

    transition: background 0.8s;
}

.skills-content {
    width: 400px;
    height: 100vh;
    padding: 50px 25px;
    box-sizing: border-box;
    background: white;

    position: absolute;
    top: 0px;
    left: -400px;

    font-size: 15px;
    line-height: 25px;
    text-align: right;

    transition: left 0.8s;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.skills-slide {
    left: 0px;
}

ul, li {
    margin: 0px;
    padding: 0px;
    list-style-type: none ;
}

label {
    font-family: PT Mono;
    display: block;
    font-weight: bold;
    margin-top: 25px;
}

.hide {
    visibility: hidden;
}

.fade-out {
    background: none;
}

@media only screen and (max-width: 450px) {
    .skills-content {
        width: 80%;
        align-items: flex-start;
        padding: 10px 20px;
    }

    .skills-content label {
        margin-top: 10px;
    }
}