.sidebar {
    /* background: url('./path.jpg'); */
    background: url('https://scontent-lga3-2.cdninstagram.com/v/t51.2885-15/e35/39323464_296104134311533_3166859299225862144_n.jpg?_nc_ht=scontent-lga3-2.cdninstagram.com&_nc_cat=101&_nc_ohc=xF8n_8ZuLMoAX9un2ux&tp=1&oh=822bc131223790479e87a432b694a80b&oe=603793E8');
    background-repeat: no-repeat;
    background-size: cover !important;
    background-position: left !important;

    position: relative;
}

.email {
    display: inline-block;
    color: rgb(128, 128, 128);
    text-decoration: none;
    padding: 2px 15px 0px 0px;
}

.email:hover {
    color: white;
}

.logos {
    box-sizing: border-box;
    padding: 10px;
    position: fixed;
    top: 0px;
    right: 0px;

    display: flex;
}

.logos a {
    text-decoration: none;
    margin-right: 10px;
}

.logos a svg {
    width: 20px;
    fill: rgb(128, 128, 128);
}

.logos a svg:hover {
    fill: white;
}

nav {
    transform: rotate(-90deg);
    font-size: 15px;
    position: absolute;
    background: black;
    padding: 10px 20px;
    box-sizing: border-box;
    border-radius: 5px 5px 0px 0px;


    bottom: 250px;
    right: -110px;

    /* when adding all projects link
    right: -160px; */

    width: 280px;
}

nav li {
    cursor: pointer;
    display: inline-block;
    text-decoration: none;
    color: rgb(128, 128, 128);
    padding: 10px 15px;
    box-sizing: border-box;
    border-right: 1px solid rgb(42, 42, 42);
    font-size: 15px;
}

nav li a {
    color: rgb(128, 128, 128);
    margin: 0px;
    padding: 0px;
    text-decoration: none;
}

nav li:first-child {
    border-radius: 10px 0px 0px 10px;
}

nav li:last-child {
    border-right: none;
    border-radius: 0px 10px 10px 0px;
}

nav li:hover, nav li a:hover {
    color: white;
}

@media only screen and (max-width: 730px) {

    .sidebar, .content {
        width: 100%;
    }

    .email {
        width: 100%;
    }

    .logos, nav {
        width: fit-content;
    }

    .logos {
        position: initial;
        width: 100%;
        height: fit-content;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        padding: 50px 0px;
    }

    .logos, .sidebar nav {
        width: 100%;
    }

    .email {
        margin-bottom: 15px;
        text-align: center;
        margin-right: 0px !important;
        padding-right: 0px;
    }

    .sidebar {
        display: flex;
        flex-direction: column-reverse;
        justify-content: center;
        position: fixed;
        z-index: 10;
        right: 0px;
        top: -100%;
        background: #151515 !important;
        height: fit-content;
        transition: top 0.5s;
    }

    .sidebar-down {
        top: 0px;
    }

    .contact-close, .contact-label {
        background: none;
        border: none;
        font-size: 13px;
        font-weight: normal;
        cursor: pointer;
    }

    .contact-close {
        position: absolute;
        bottom: 0px;
        right: 0px;
        padding: 10px 20px;
        background: black;
        color: white;
    }

    .contact-label {
        display: block;
        position: fixed;
        top: 0px;
        right: 0px;
        padding: 20px;
        fill: white;
    }

    .sidebar nav {
        position: relative;
        transform: none;
        bottom: auto;
        right: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 50px;
        background: none;
    }

    .sidebar nav li {
        color: rgb(128, 128, 128);
        border: none;
        padding: 10px;
        font-size: 19px;
    }

    .sidebar nav li:hover {
        color: white;
        background: none;
        border-radius: 0px;
    }
}