.single {
    border-radius: 10px;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    overflow: hidden;
}

.single-content, .single-project {
    height: 100%;
    width: 50%;
    box-sizing: border-box;
}

.single-content {
    background: linear-gradient(rgba(0,0,0,0), rgba(0,0,0,0.5));
    font-size: 14px;
    box-sizing: border-box;
    padding: 20px 15px 10px;
    display: flex;
    flex-direction: column;
}

.single-content h3 {
    margin: 0px;
    color: white;
    font-weight: normal;
    margin-bottom: 5px;

    font-family: "PT Mono";
    font-size: 15px;
}

.single-content ul {
    margin: 0px;
    padding: 0px;
}

.single-content li {
    list-style: none;
    display: block;
    font-size: 13px;
}

.single-content li:first-child {
    padding-left: 0px;
}
.single-content li:last-child {
    border-right: 0px;
}



/* PROJECT */

.single-project {
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
}

.project-image {
    height: 100%;
    background-repeat: no-repeat;
    background-position: left;
    background-size: cover;
}

.description {
    background: linear-gradient(rgba(0,0,0,0), rgba(0,0,0,0.7));
    height: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    overflow: scroll;
    box-sizing: border-box;
}

.description p {
    margin: 0px;
    padding: 15px;
    padding-bottom: 0px;
    box-sizing: border-box;
}

.project-links {
    margin-top: 10px;
    box-sizing: border-box;
    height: fit-content;
}

.project-links  svg{
    fill: white;
    width: 20px;
    margin-right: 5px;
    box-sizing: border-box;
}

.project-links  a {
    width: 100%;
    font-size: 15px;
    padding: 5px 10px;
    box-sizing: border-box;

    text-decoration: none;
    color: white;

    display: flex;
    align-items: center;
}

.project-links a:hover {
    background: rgba(0,0,0,0.2);
}

@media only screen and (max-width: 450px) {
    .projects {
        padding: 2px;
    }

    .single {
        flex-direction: column;
        width: calc( 50% - 2px);
        height: calc( 50% - 2px);
        border-radius: 10px;
    }

    /* top row */
    .single:nth-child(1), .single:nth-child(2){
        margin-bottom: 1px;
    }

    /* bottom row */
    .single:nth-child(3), .single:nth-child(4){
        margin-top: 1px;
    }

    /* left column */
    .single:nth-child(1), .single:nth-child(3){
        margin-right: 1px;
    }

    /* right column */
    .single:nth-child(2), .single:nth-child(4){
        margin-left: 1px;
    }

    .single-content, .single-project {
        width: 100%;
    }

    .single-project {
        height: 66%;
    }

    .single-content {
        height: 33%;
        padding: 0px;
        display: flex;
        justify-content: center;
    }

    .single-content h3 {
        margin: 0px;
        padding: 10px;
        display: flex;
    }

    .single-content ul, .description p {
        display: none
    }

    .description {
        overflow: auto;
    }

    .project-links {
        margin: 0px;
        height: 100%;
        display: flex;
        flex-direction: column;
    }

    .project-links a {
        height: 100%;
    }

    .project-links svg {
        margin-right: 10px;
    }
}