.info {
    font-family: "PT Mono";
    font-size: 16px;
    font-weight: normal;

    box-sizing: border-box;
    color: rgb(128, 128, 128);
    position: static;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: linear-gradient(#121212, black);
}

.info h1 {
    color: #E34344;
    font-size: 20px;
    font-weight: normal;
}

.info i {
    color: white;
}

.info p{
    width: fit-content;
    margin: auto;
    font-size: 14px;
    line-height: 25px;
    font-weight: normal;
}

.profile-pic {
    pointer-events: none; 
    box-sizing: border-box;
    border: 5px solid #151515;
    overflow: hidden;
    width: 100px;
    height: 100px;
    border-radius: 100px;

    top: calc(25% - 100px/2 );
    left: calc( 50% - 100px/2 );
    position: absolute;

    z-index: 5;

    transition: all 0.8s;
    pointer-events: none;
    touch-action: none;
}

.profile-pic img {
    width: 100%;
    pointer-events: none;
    touch-action: none;
}

.profile-pic-about {
    width: 300px;
    height: 300px;
    border-radius: 300px;

    top: calc(25% - 300px/2 );
    left: calc( 50% - 300px/2 );
    pointer-events: none;
    touch-action: none;
}

@media only screen and (min-height: 1200px) {
    .profile-pic {
        top: calc(25% - 150px/2 );
        left: calc( 50% - 150px/2 );

        width: 150px;
        height: 150px;
        border-radius: 150px;
    }

    .profile-pic-about {
        width: 300px;
        height: 300px;
        border-radius: 300px;

        top: calc(25% - 300px/2 );
        left: calc( 50% - 300px/2 );
    }
}

@media only screen and (min-width: 2000px) {
    .profile-pic {
        left: calc(100% - 1100px);
        top: calc(25% - 200px/2 );

        width: 200px;
        height: 200px;
        border-radius: 200px;
    }

    .profile-pic-about {
        left: calc( 50% - 200px );
        top: calc(25% - 225px );
        width: 400px;
        height: 400px;
        border-radius: 400px;
    }
}

@media only screen and (max-width: 950px) {
    .profile-pic {
        left: calc(100% - 650px);
    }

    .profile-pic-about {
        left: calc( 50% - 300px/2 );
    }
}

@media only screen and (max-width: 730px) {
    .profile-pic {
        top: 25px;
        left: calc( 50% - 100px/2 );
    }

    .profile-pic-about {
        top: calc(25% - 300px/2 );
        left: calc( 50% - 300px/2 );
    }
}

@media only screen and (max-width: 450px) {
    .profile-pic {
        width: 125px;
        height: 125px;
        top: 20px;
        left: calc( 50% - 125px/2 );
    }

    .profile-pic-about {
        top: 5px;
        left: calc( 50% - 200px/2 );
        width: 200px;
        height: 200px;
        border-radius: 200px;
    }

    .info {
        padding-top: 130px;
        text-align: center;
    }

    .info h1 {
        font-size: 20px;
        margin: 0px;
    }

    .info p {
        font-size: 20px;
        line-height: 25px;
    }
}