.projects {
    background: black;
    color: white;

    box-sizing: border-box;
    padding: 10px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.single {
    box-sizing: border-box;
    width: calc(50% - 5px);
    height: calc(50% - 5px);
    border-radius: 5px;
}


/* top row */
.single:nth-child(1), .single:nth-child(2){
    margin-bottom: 5px;
}

/* bottom row */
.single:nth-child(3), .single:nth-child(4){
    margin-top: 5px;
}

/* left column */
.single:nth-child(1), .single:nth-child(3){
    margin-right: 5px;
}

/* right column */
.single:nth-child(2), .single:nth-child(4){
    margin-left: 5px;
}

@media only screen and (min-height: 1200px) {
    .info{
        height: calc(100% - 700px) ;
    }
    .projects {
        height: 700px;
    }
}
